import React, { PureComponent, Fragment} from 'react';
import { connect } from 'react-redux';
import './pokemonFirstCity.scss';
import map from './sprites/map.png';
import hero from './sprites/jiwoo.png';
import startVilageSound from './sound/start-vilage.mp3';
import SpriteSheet from './SpriteSheet.js';

class PokemonFirstCity extends PureComponent{
    constructor(props) {
        super(props);
        this.state = {
            isMobile: /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent),
            screen : {
                width: 0,
                height: 0,
                x: -995,
                y: -4220
            },
            context:{},
            character:{
                speed: 3,
                width: 16,
                height: 16,
                x: 0
            },
            map:{
                img: null
            },
            peoples:{
                hero:{
                    img: null
                }
            }
        }
    }
    //https://codepen.io/Gthibaud/pen/JaEMGW
    //https://codepen.io/Gthibaud/pen/ryQRYP
    //https://codepen.io/mildrenben/pen/EPGBNb
    //https://codepen.io/branneman/pen/bJIzk
    
    //https://codepen.io/32teeth/pen/bGGMxyb
    //(https://github.com/32teeth/hud-gamepad)

    //https://codepen.io/Mobius1/details/PpJPKE
    //https://codepen.io/creme/pen/EqeXNJ
    //https://codepen.io/MysticReborn/details/rygqao

    //https://codepen.io/kaista/pen/LBLVgb

    //https://imgur.com/spz3zUx
    //pokemon sprites 
    //https://rpgtileset.com/sprite/pokemon-sprites/
    //https://veekun.com/dex/downloads

    //https://www.youtube.com/watch?v=JEYEpledOxs
    //https://www.youtube.com/watch?v=g-FpDQ8Eqw8
    
    componentDidMount() {
        if(!document.getElementById('pokemon-firstcity')) return;
        
        //시작시 포커스
        const gameScreen = document.getElementById('pokemon-screen-box');
        if (gameScreen) {
            gameScreen.focus();
        }
        
        //초기 사운드 볼륨 설정
        const bgSound = document.getElementById('pokemon-start-vilage-sound');
        if (bgSound) {
            bgSound.volume = this.props.common.sound.volume / 100;
            console.log(this.props);
        }

        
        const canvas = document.getElementById('pokemon-firstcity');
        const ctx = canvas.getContext("2d");
        // const image = new Image();
        // image.src = map;
        // ctx.drawImage(image, 0 , 0);
        console.log('@@@@@@@' ,map);
        this.setState({
            ...this.state,
            screen : {
                ...this.state.screen,
                width: canvas.width,
                height: canvas.height
            },
            context:ctx
        });
        const width = canvas.width;
        const height = canvas.height;
        const charWidth = this.state.character.width;
        const charHeight = this.state.character.height;
        const x = this.state.character.x;
        
        ctx.strokeStyle = 'red';
        ctx.lineWidth = 1;
        ctx.lineCap = "round";
        // ctx.clearRect(0, 0, canvas.width, canvas.height);
        // ctx.beginPath();
        
        // ctx.fillRect(0,0,16,16);

        
        // ctx.moveTo(x, height - charHeight);
        // // 선 긋기
        // ctx.lineTo(x + charWidth, height - charHeight); 
        // ctx.lineTo(x + charWidth, height);
        // ctx.lineTo(x, height);
        // ctx.lineTo(x, height - charHeight);

        // ctx.stroke();
        // ctx.closePath();
        // -1000 , -4200
        // this.loadImage(map).then(image => {ctx.drawImage(image, this.state.screen.x, this.state.screen.y, 6528, 6400)});

        //Map
        this.loadImage(map).then(image => {
            this.setState({ ...this.state, map: { img: image } });
            ctx.drawImage(image, this.state.screen.x, this.state.screen.y, 6528, 6400)
        });

        // Hero
        this.loadImage(hero).then(image => {
            const sprites = new SpriteSheet(image, 16, 16);
            sprites.define('hero', 0, 0);
            sprites.draw('hero', ctx, 16, 16);
            this.setState({ ...this.state, peoples: { hero: { img: image } } });
            ctx.drawImage(image, -100, -100, 868, 908);
        });

        // console.log(ctx);
    }

    //사운드 사운드 볼륨 조절 
    componentDidUpdate(prevProps, prevState) {
        if (!document.getElementById('pokemon-start-vilage-sound')) return;
        if (this.props.common.sound.volume !== prevProps.common.sound.volume) {
            document.getElementById('pokemon-start-vilage-sound')
                .volume = this.props.common.sound.volume / 100;
        }
    }
    loadImage = (url) => {
        return new Promise( resolve => {
            const img = new Image();
            img.addEventListener('load',()=>{
                resolve(img)
            });
            img.src = url;
       });
    }
    handleMapMove = ({x, y}) => {
        if(!x || !y) return;
        this.setState({
            ...this.state,
            screen: {
                ...this.state.screen,
                x,
                y
            }
        });
        this.state.context.drawImage(
            this.state.map.img,
            x || this.state.screen.x,
            y || this.state.screen.y,
            6528,
            6400
        )
    }
    handleKeyPress = (e, code) =>{
        console.log('> handleKeyPress');
        const ctx = this.state.context;
        // ctx.beginPath();
        let isMove = false;
        let screenX = this.state.screen.x;
        let screenY = this.state.screen.y;
        switch (code || e.which) {
            case 37: //left
                try{
                    ctx.fillRect(16,0,16,16);
                    isMove = true;
                    screenX = screenX + 16;
                }catch(e){
                    return ;
                }
                break;
            case 38: //up
                try{
                    ctx.fillRect(0,-16,16,16);
                    isMove = true;
                    screenY = screenY + 16;
                }catch(e){
                    return ;
                }
                break;
            case 39: //right
                try{
                    ctx.fillRect(-16,0,16,16);
                    isMove = true;
                    screenX = screenX - 16;
                }catch(e){
                    return ;
                }
                break;
            case 40: //down
                try{
                    ctx.fillRect(0,16,16,16);
                    isMove = true;
                    screenY = screenY - 16;
                }catch(e){
                    return ;
                }
                break;
            default:
        }
        if(isMove){
            this.handleMapMove({ x: screenX, y: screenY});
        }
    }
    change = () => {
        const x = document.getElementById('x');
        const y = document.getElementById('y');
        // this.setState({...this.state, screen: {...this.state.screen, x: x.value, y: y.value } });
        this.loadImage(map).then(image => {this.state.context.drawImage(image, x.value, y.value, 6528, 6400)});
    }
    mobileMouseDown = (e , code) =>{
        this.handleKeyPress(e, code);
    }
    render(){
        let mobileController;
        if (this.state.isMobile) {
            mobileController = (
                <Fragment>
                    <div className="mobile-joystick">
                        <div className="mobile-controller">
                            <div><button onMouseDown={e => this.mobileMouseDown(e, 38)}></button></div>
                            <div><button onMouseDown={e => this.mobileMouseDown(e, 39)}></button></div>
                            <div><button onMouseDown={e => this.mobileMouseDown(e, 40)}></button></div>
                            <div><button onMouseDown={e => this.mobileMouseDown(e, 37)}></button></div>
                        </div>
                    </div>
                    {/* <div className="mobile-joyBtns">
                        <div className="mobile-joy-btn">
                            <div><button>O</button></div>
                            <div><button>X</button></div>
                        </div>
                    </div> */}
                </Fragment>
            );
        }
        return (
            <Fragment>
                <audio autoPlay="ture" loop={true} id="pokemon-start-vilage-sound">
                    <source src={startVilageSound} type='audio/mpeg' />
                </audio>
                <div id="pokemon-screen-box" tabIndex="0" onKeyDown={this.handleKeyPress}>
                    {mobileController}
                    {/* <p>x</p><input id="x"/>
                    <p>y</p><input id="y"/>
                <button onClick={this.change}>Click</button> */}
                    <canvas id="pokemon-firstcity" />
                </div>
            </Fragment>
        );
    }
} 
function mapStateToProps(state) {
    return state.redux_commonInfo;
}

export default connect(mapStateToProps, null)(PokemonFirstCity);