/* eslint-disable no-unused-vars */

import React, { PureComponent, Fragment } from 'react';
import {connect} from 'react-redux';
import './pokemon.scss'; 
// import './rendomVertion';
import PokemonVillage from '../pokemon-village';
import PokemonFirstCity from '../pokemonFirstCity';

import introMp3 from './sound/intro.mp3';
import startHit from './sound/start-hit.mp3';

//포켓몬스터 게임
class Pokemon extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            screenIndex: 0
        }
    }
    //초기 사운드 볼륨 설정
    componentDidMount() {
        if(document.getElementById('pokemon-intro-sound'))
            document.getElementById('pokemon-intro-sound')
                .volume = this.props.common.sound.volume / 100;

        if (document.getElementById('pokemon-start-sound'))
            document.getElementById('pokemon-start-sound')
            .volume = this.props.common.sound.volume / 100;
    }
    //사운드 사운드 볼륨 조절 
    componentDidUpdate(prevProps, prevState) {
        if(!document.getElementById('pokemon-intro-sound')) return;
        if (this.props.common.sound.volume !== prevProps.common.sound.volume){
            document.getElementById('pokemon-intro-sound')
                .volume = this.props.common.sound.volume / 100;
        }
    }
    //게임 시작
    onGameStart = () => {
        document.getElementById("start-txt").classList.add('start-glitter');
        document.getElementById("pokemon-start-sound").play();
        window.setTimeout(()=>{
            if (document.getElementById("pokemon-intro-sound")){
                document.getElementById("pokemon-intro-sound").pause()
            }
            this.handleNextScreen();
        },1500);
    }
    //Start 버튼 클릭 시
    handleNextScreen = () => {
        // this.setState({...this.state, screenIndex : this.state.screenIndex + 1 });
        this.setState({...this.state, screenIndex : this.state.screenIndex + 2 });
        this.onScreen();
    }
    onScreen = () => {
        switch (this.state.screenIndex) {
            case 0:
                //intro
                return (
                    <Fragment>
                        <audio id="pokemon-start-sound">
                            <source src={startHit} type='audio/mpeg'/>
                        </audio>
                        <audio autoPlay="ture" id="pokemon-intro-sound">
                            <source src={introMp3} type='audio/mpeg'/>
                        </audio>
                        <div className='wrapper'>
                            <div className='composition'>
                                <div className='scene scene--1'>
                                    <div className='shooting-star'></div>
                                    <div className='gamefreak-logo'></div>
                                    <div className='gamefreak'></div>
                                    <div className='star-shower-mask'>
                                        <div className='star-shower--light'></div>
                                        <div className='star-shower--dark'></div>
                                    </div>
                                </div>
                                <div className='scene scene--2'>
                                    <section className='opponent jigglypuff-wrapper'>
                                        <figure className='jigglypuff'></figure>
                                        <figure className='jigglypuff--flinch'></figure>
                                        <figure className='jigglypuff--tackle'></figure>
                                    </section>
                                    <section className='opponent nidorino-wrapper'>
                                        <figure className='nidorino'></figure>
                                        <figure className='nidorino--flinch'></figure>
                                        <figure className='nidorino--tackle'></figure>
                                    </section>
                                    <section className='gengar-wrapper'>
                                        <figure className='gengar'></figure>
                                        <figure className='gengar--scratch1'></figure>
                                        <figure className='gengar--scratch2'></figure>
                                    </section>
                                </div>
                                <div className='scene scene--3'>
                                    <div className='logo-wrapper'>
                                        <figure className='pokemon-logo'></figure>
                                        <span><strong>BACK Version</strong></span>
                                        <span className="start-btn" 
                                        onClick={this.onGameStart}><i id="start-txt">▶Start</i></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Fragment>);
            case 1:
                return <PokemonVillage />;
            case 2:
                return <PokemonFirstCity />;
            default:
                break;
        }
    }
    render(){
        return (
            <div className="pop_content">
                <div className="pop_preview">
                    {this.onScreen()}
                </div>
            </div>
        )
    }
}


function mapStateToProps(state) {
    return state.redux_commonInfo;
}

export default connect(mapStateToProps,null)(Pokemon);

